<template>
  <transition name="slide">
    <music-list  :songs="songs" :father-image="bgImage" :title="title"></music-list>
  </transition>
</template>

<script>
  import MusicList from '../music-list/music-list.vue'
  import {getSingerDetail} from '../../api/singer'
  import {ERR_OK} from 'api/config'
  import {mapGetters} from 'vuex'
  import {createSong} from '../../common/js/song.js'


  export default {
    data() {
      return {
        songs: []
      }
    },
    created() {
      this.receiveSinger();
    },
    computed: {
      //歌手名称
      title() {
        return this.singer.name
      },
      //歌手代表图
      bgImage() {
        return this.singer.avatar
      },
      ...mapGetters([
        'singer'
      ])
    },
    methods: {
      //接收一个singer数据
      receiveSinger() {
        if (!this.singer.id) {
          this.$router.push({
            path: '/singer-page'
          });
          return;
        }
        getSingerDetail(this.singer.id).then((res) => {
          if (res.code === ERR_OK) {
            this.songs = this._normalizeSongs(res.data.list);
          }
          else {
            console.log("请求歌手详情数据error")
          }
        });
      },
      //辅助处理数据
      //歌曲数据列表转换成歌曲类
      _normalizeSongs(list) {
        let ret = [];
        list.forEach((item) => {
          if (item.musicData.songid && item.musicData.albummid) {
            //如果直接用构造函数new，需要传入一串函数，
            // 所以用另一个函数createSong，传入一个参数，进而用createSong调用构造函数new
            ret.push(createSong(item.musicData))
          }
        });
        return ret;
      }
    },
    components: {
      MusicList
    }
  }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)

</style>



